import React, { Component } from 'react'
import { Tabs, Panel, ActionSheetItem, PanelHeader, Div, Button, FixedLayout, ActionSheet, FormLayout, Input, TabsItem, Group, Title, Cell, RichCell} from '@vkontakte/vkui';

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';
import Icon28UserOutline from '@vkontakte/icons/dist/28/user_outline';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';

import {Howl, Howler} from 'howler';

import How_Work from '../components/How_Work';
import Missia from '../components/Missia';
import Programm from '../components/Programm';
import Vitr from '../components/Vitr';
import My_vybor from '../components/My_vybor';
import Market from '../components/Market';
import Friends_help from '../components/Friends_help';
import Service from '../components/Service';
import Cashback from '../components/Cashback';

export default class Help extends Component {
    state = {
        activeTab: 'about',
        activeTabView: 'about',
    }

    componentDidMount() {
        this.props.stop_audio();
    }

    openMenu = () => {
        this.props.setPopout(
        <ActionSheet onClose={() => this.props.setPopout(null)}>
            <ActionSheetItem before={<Icon28ViewOutline />} autoclose  onClick={() => this.setState({ activeTab: 'about'})}>  ?</ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose  onClick={() => this.setState({ activeTab: 'mis'})}></ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose  onClick={() => this.setState({ activeTab: 'market' })}> " "</ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'vitr' })}> ""</ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'my_vybor' })}> " "</ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'unic' })}> </ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'friends' })}> ?</ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'service' })}></ActionSheetItem>
            <ActionSheetItem before={<Icon28ViewOutline />}  autoclose onClick={() => this.setState({ activeTab: 'cashback' })}> </ActionSheetItem>
        </ActionSheet>    
        )
    }

    openMenu_2 = () => {
        this.props.setPopout(
            <ActionSheet onClose={() => this.props.setPopout(null)}>
                <ActionSheetItem before={<Icon28ViewOutline />} autoclose  onClick={() => this.setState({ activeTab: 'trevelpayouts'})}>  Travelpayouts</ActionSheetItem>
                <ActionSheetItem before={<Icon28ViewOutline />}  autoclose >  Booking</ActionSheetItem>
                <ActionSheetItem before={<Icon28ViewOutline />}  autoclose >  Ozon</ActionSheetItem>
                <ActionSheetItem before={<Icon28ViewOutline />}  autoclose  onClick={() => this.setState({ activeTab: 'cashback', activeTabView: 'about' })}>   </ActionSheetItem>
            </ActionSheet>    
            )
    }


    openMenuUser() {
        this.props.setPopout(     
            <ActionSheet onClose={() => this.props.setPopout(null)}>
            <ActionSheetItem before={<Icon28UserCircleOutline />} autoclose onClick={() => this.props.openModal('status')}>
                
            </ActionSheetItem>
            <ActionSheetItem before={<Icon28UserCircleOutline />} onClick={() => this.props.onChangeGroups('theme',(this.props.state.theme == 'bright_light' ? 'space_gray' : 'bright_light'))} autoclose>
                 {this.props.state.theme == 'bright_light' ? '': ''} 
            </ActionSheetItem>
            <ActionSheetItem data-story='subscribes' onClick={() => this.props.onChangeGroups('activeStory', 'subscribes')} before={<Icon28UserCircleOutline />} autoclose>
                 
            </ActionSheetItem>
          </ActionSheet>
          )
    }

    render() {
        const {activeTab, activeTabView } = this.state;
        const { id, state, marker } = this.props;
        const list = [
            {group: ' ', button: ' ', activeMenu: 'packet',data: [
                {name: 'travelata.com', viget: 'travelata',f: '3.8% ',href: `https://tp.media/r?marker=${marker}&p=771&u=https%3A%2F%2Ftravelata.ru%2F`},
        
                {name : 'onlinetours.ru', viget: 'onlinetours',f: '3,8%',href: `https://tp.media/r?marker=${marker}&p=1094&u=https%3A%2F%2Fwww.onlinetours.ru%2F`},
        
                {name : 'level.travel', viget: 'level',f: '4,25%', href:`https://tp.media/r?marker=${marker}&p=660&u=https%3A%2F%2Flevel.travel%2F`}
            ]},
            {group:', , ,   .', button: ' ' ,activeMenu: 'hotel',data: [
                {name: 'booking.com', viget: 'booking.com',f:'4%', href:`https://tp.media/r?marker=${marker}&p=3650&u=https%3A%2F%2Fbooking.com`},
                {name : 'sutochno.ru', viget: 'sutochno',f: '5%', href: `https://tp.media/r?marker=${marker}&p=2690&u=https%3A%2F%2Fsutochno.ru%2F`},
                {name:'Hotels.com', viget: 'Hotels',f: '4,8%', href:`https://tp.media/r?marker=${marker}&p=3247&u=https%3A%2F%2Fru.hotels.com`},
                {name:'hotellook.ru', viget: 'hotellook',f:'50%', href:`https://tp.media/r?marker=${marker}&p=4115&u=https%3A%2F%2Fhotellook.com`}
            ]}, 
            {group: '', button: ' ', activeMenu: 'st', data: [
                {name: 'sravni.ru',viget: 'sravni',f: '13,6%', href: `https://tp.media/r?marker=${marker}&p=1416&u=https%3A%2F%2Fwww.sravni.ru%2F`},
                {name:'cherehapa.ru', viget: 'cherehapa',f:'15%', href:`https://tp.media/r?marker=${marker}&p=659&u=https%3A%2F%2Fcherehapa.ru%2F`},
                {name: 'strahovkaru.ru', viget: 'strahovkaru',f: '25%', href: `https://tp.media/r?marker=${marker}&p=1582&u=https%3A%2F%2Fstrahovkaru.ru`},
                {name: 'tripinsurance.ru', viget: 'tripinsurance',f:'25%', href: `https://tp.media/r?marker=${marker}&p=1588&u=https%3A%2F%2Fwww.tripinsurance.ru%2F`}
            ]}, 
            {group: ' ', button: ' ', activeMenu: 'procat', data: [
                {name: 'discovercars.com',viget: 'discovercars',f:  '56%', href: `https://tp.media/r?marker=${marker}&p=3555&u=https%3A%2F%2Fwww.discovercars.com%2F`},
                {name: 'economybookings.com', viget: 'economybookings',f: '56%', href:`https://tp.media/r?marker=${marker}&p=2018&u=https%3A%2F%2Fwww.economybookings.com%2F`},
                {name: 'autoeurope.ru', viget: 'autoEurope',f: '8%', href:`https://tp.media/r?marker=${marker}&p=1591&u=https%3A%2F%2Fwww.autoeurope.ru`}
            ]}, 
            {group: '', button:' ', activeMenu: 'transfers_2', data: [
                {name: 'intui.travel',viget: 'intui', f: '6,5%', href: `https://tp.media/r?marker=${marker}&p=657&u=https%3A%2F%2Fwww.intui.travel%2F`},
                {name: 'iway.ru', viget: 'iway', f: '7%', href: `https://tp.media/r?marker=${marker}&p=4235&u=https%3A%2F%2Fiway.ru`},
                {name: 'kiwitaxi.ru', viget:'kiwitaxi', f: '50%', href:`https://tp.media/r?marker=${marker}&p=647&u=https%3A%2F%2Fkiwitaxi.ru`}
            ]},
            {group: ' ',button:' ', activeMenu: null, viget: 'dreamlines', data: [
                {name: 'dreamlines.ru', f: '3,5%', href: `https://tp.media/r?marker=${marker}&p=1919&u=https%3A%2F%2Fwww.dreamlines.ru%2F`}
            ]}, 
            {group: '', activeMenu:'excursion_and_m',button:' ',data: [
            {name: 'tezeks.com' ,viget: 'tezeks',f: '8%', href:  `https://tp.media/r?marker=${marker}&p=4268&u=https%3A%2F%2Ftezeks.com%2F`},
            {name: 'tripster.ru' ,viget: 'tripster',f: '8%', href:`https://tp.media/r?marker=${marker}&p=652&u=https%3A%2F%2Fexperience.tripster.ru`},
            {name: 'musement.com',viget: 'musement',f: '8%', href: `https://tp.media/r?marker=${marker}&p=2015&u=https%3A%2F%2Fwww.musement.com%2F`},    
            {name: 'ticketnetwork.com',viget: 'ticketnetwork',f: '12,5%', href: `https://tp.media/r?marker=${marker}&p=1948&u=https%3A%2F%2Fwww.ticketnetwork.com%2F`},
            {name: 'tiqets.com',viget: 'tiqets',f: '6%', href: `https://tp.media/r?marker=${marker}&p=2074&u=https%3A%2F%2Fwww.tiqets.com%2Fen%2F`},
            {name: 'sputnik8.com',viget: 'sputnik8',f: null, href: `https://tp.media/r?marker=${marker}&p=656&u=https%3A%2F%2Fwww.sputnik8.com%2F`},
            {name: 'weatlas.com',viget: 'weatlas',f: '10%', href: `https://tp.media/r?marker=${marker}&p=654&u=https%3A%2F%2Fweatlas.com%2F`}
            ]}]
        return (
            <Panel id={id}>
                <PanelHeader separator={false}></PanelHeader>
                <Tabs>
                    <TabsItem selected={activeTabView === 'about'} onClick={() => this.setState({ activeTabView: 'about', activeTab: 'about' })}>  ?</TabsItem>
                    <TabsItem selected={activeTabView === 'cashback' } onClick={() => this.setState({ activeTabView: 'cashback', activeTab: 'trevelpayouts'})}></TabsItem>
                </Tabs>
                {activeTab === 'trevelpayouts' && 
                    <div>
    {list.map((item) => (
                <Group header={<Cell multiline disabled><Title weight='heavy' level='2'>{item.group}</Title></Cell>}>
                        {item.data.map((v, i) => (
                            <RichCell
                                caption={v.f}
                                multiline
                                actions={
                                    <>
                                    <Button href={v.href} target='_blank'>  </Button>
                                    <Button onClick={() => this.props.goViget({...v,i, activeMenu: item.activeMenu, })}>{item.button}</Button>
                                    </>
                                }
                            >
                                {v.name}
                            </RichCell>
                        ))}
                </Group>
            ))}
            <FormLayout>
                <Input top=' trevelpayouts'/>
                <Button size='xl'>x</Button>
            </FormLayout>
                    </div>
                }
                {activeTab == 'about' && <How_Work sex={this.props.state.user.sex}/>}
                {activeTab == 'mis' && <Missia sex={this.props.state.user.sex} />}
                {activeTab == 'unic' && <Programm sex={this.props.state.user.sex} />}
                {activeTab == 'vitr' && <Vitr sex={this.props.state.user.sex} />}
                {activeTab == 'my_vybor' && <My_vybor sex={this.props.state.user.sex} />}
                {activeTab == 'market' && <Market sex={this.props.state.user.sex} />}
                {activeTab == 'friends' && <Friends_help sex={this.props.state.user.sex} />}
                {activeTab == 'service' && <Service sex={this.props.state.user.sex} />}
                {activeTab == 'cashback' && <Cashback sex={this.props.state.user.sex} go={() => this.setState({ activeTabView: 'cashback', activeTab: 'trevelpayouts'})} state={this.props.state} />}

                <FixedLayout vertical='bottom'>
                    <div style={{ display:'flex', justifyContent:'center'}}>
                    <div style={{ paddingBottom:6 }}>
                        <Button className='circle_button' onClick={() => {if(activeTabView == 'about') this.openMenu(); else this.openMenu_2()}}>
                            <Icon28ViewOutline />
                        </Button>
                    </div>
                    <div style={{ paddingBottom:6,paddingRight:32,right:'0%', position:'absolute' }}>
                        <Button className='circle_button' style={{backgroundColor:'#4BBDE7'}} onClick={() => this.openMenuUser()}>
                            <Icon28UserOutline />
                        </Button>
                    </div>
                    </div>
                </FixedLayout>
                <Div />
                <Div />
            </Panel>
        )
    }
}